  'Inflatable Sleeping Pod': { inputs: { power: 0.0001 }, outputs: { wellbeing: 0.0023 }, slider: 0.75, health: 1.0 },
  'Rover': { inputs: { power: 0.0001 }, slider: 0.75, health: 1.0 },
  'Scouting Drone': { inputs: { power: 0.0001 }, slider: 0.75, health: 1.0 },
  'Buggy': { inputs: { power: 0.0001 }, slider: 0.75, health: 1.0 },
  'Fuel Plant': { inputs: { power: 0.0007, water: 0.0005 }, outputs: { oxygen: 0.0004, fuel: 0.0006 }, slider: 0.75, health: 1.0 },
