  'Inflatable Greenhouse': { inputs: { power: 0.0006, water: 0.0006 }, outputs: { food: 0.0020 }, slider: 0.75, health: 1.0 },
  'Male Engineer': { inputs: { water: 0.0001, wellbeing: 0.0012, food: 0.0003, oxygen: 0.0001 }, outputs: { engineer_labor: 0.0011 }, slider: 0.75, health: 1.0 },
  'Female Engineer': { inputs: { water: 0.0001, wellbeing: 0.0012, food: 0.0003, oxygen: 0.0001 }, outputs: { engineer_labor: 0.0011 }, slider: 0.75, health: 1.0 },
  'Male Scientist': { inputs: { water: 0.0001, wellbeing: 0.0012, food: 0.0003, oxygen: 0.0001 }, outputs: { science_labor: 0.0011 }, slider: 0.75, health: 1.0 },
  'Female Scientist': { inputs: { water: 0.0001, wellbeing: 0.0012, food: 0.0003, oxygen: 0.0001 }, outputs: { science_labor: 0.0011 }, slider: 0.75, health: 1.0 },
  'Male Botanist': { inputs: { water: 0.0001, wellbeing: 0.0012, food: 0.0003, oxygen: 0.0001 }, outputs: { food: 0.0005, science_labor: 0.0009 }, slider: 0.75, health: 1.0 },
  'Female Botanist': { inputs: { water: 0.0001, wellbeing: 0.0012, food: 0.0003, oxygen: 0.0001 }, outputs: { food: 0.0005, science_labor: 0.0009 }, slider: 0.75, health: 1.0 },
  'Male Doctor': { inputs: { water: 0.0001, wellbeing: 0.0012, food: 0.0003, oxygen: 0.0001 }, outputs: { social_labor: 0.0009, medicine: 0.0002 }, slider: 0.75, health: 1.0 },
  'Female Doctor': { inputs: { water: 0.0001, wellbeing: 0.0012, food: 0.0003, oxygen: 0.0001 }, outputs: { social_labor: 0.0009, medicine: 0.0002 }, slider: 0.75, health: 1.0 },
